.display{
    position: relative;
    display: flex;
}

.main{
    position: relative;
    display: block;
    padding-right: 40px;
}

.name{
    text-align: unset;
    margin: 20px 0px;
}

.p {
    text-transform: none;
    white-space: wrap;
}

.text{
    max-width: 950px;
    height: 500px;
    overflow: hidden;
    overflow-y: auto;
}

.container {
    overflow: hidden;
    max-height: 75vh; 
    overflow-y: auto;
    padding: 0px 40px 15vh 0px;
  }


.scrollContents{
    position: relative;
    width: 100%;    
    height: auto;
    justify-content:center;
    align-items: center;
}

.titles{
    font-size: 52px;
}

.options{
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
}

.left{
    display: grid;
    border-bottom: 0.5px solid var(--font-color);
    min-width: 200px;
    width: 12%;
    height: 385px;
    margin: 100px 60px 0px 0px;
}

.right{
    display: grid;
    min-width: 540px;
    width: 75%;
    justify-content: center;
}

.button{
    border: none;
    border-top: 0.5px solid var(--font-color);
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 1px;
    background: transparent;
    padding: 28px 10px;
    &:hover{
        cursor: pointer;
        background: var(--background);
        transition: 0.2s ease-in-out;;
    }
}


.account, .orders, .preferences, .addresses{
    display: none;
    &.show{
        display: grid
    }
}
.input{
    font-size: 12px;
    width: 540px;
    height: 46px;
    padding: 15px 12px;
    margin-bottom: 15px;
    background: rgba(0, 0, 0, 0.2);
    border: none;
    color: var(--font-color);
    &::placeholder{
    text-transform: uppercase;
    }
}

.h3{
    text-align: center;
    margin: 0px 0px 21.28px 0px;
}

@media (max-width: 1300px) {
    .right{
        min-width: 500px;
    }
}

