.footer {
    position: relative;
    bottom: 0;
    transform: rotate(-90deg);
    display: flex;
    border-top: 1px solid var(--font-color);
    width: 100vh;
    height: 40px;
    background-color: adjust-hue($color: #0000006b, $degrees: 0);
}


.ul, .ulR, .ulHide{
    position: relative;
    width: auto;
    height: auto;
    color: black;
    list-style: none;
}

.links, .title{
    text-decoration: none; //Remove underline
    font-size: 14px;
}

.links{
    margin: 0px 0px 20px;
    font-weight: 500;
}

.title{
    color: var(--font-color);
    font-weight: 600;    
}

.ulRight{
    position: relative;
    width: 100%;
    display: flex;
    justify-content: right;
}

.button{
    color: var(--font-color);
    text-transform: uppercase;
    letter-spacing: 1px;
    white-space: nowrap;
    text-decoration: none;
    font-weight: 600;
    font-style: normal;
    border: none;
    background-color: transparent;
    color: var(--font-color);
    background: transparent;
    border: none;
    font-size: 14px;
    font-weight: 100;
    margin: 0;
    cursor: pointer;
}

.boxW, .boxB, .boxR {
    display: inline-block;
    width: 10px; 
    height: 10px; 
    margin-right: 10px; 
    cursor: pointer;
}

.boxW{background-color: white; }
.boxB {background-color: black; }
.boxR {background-color: blueviolet; }

@media (max-width: 1100px) {

    .footer{
        justify-content: space-between;
    }

    .ulRight{
        width: auto;
    }
    .ulR, .ul, .ulHide{
        margin: 0px 20px 0px 0px;
        width: auto;
    }
    
}

@media (max-width: 893px) {

    .footer{
        justify-content: space-around;
    }

    .ulR, .ul, .ulHide, .ulRight{
        margin: 0px 0px 0px 0px;
        width: auto;
    }
}

@media (max-width: 820px) {
    .ulHide, .ulR{
        display: none;
    }
}
